/* 전체 컨테이너 */
.saju-intro1-container {
    background-color: #1a1a1a;
    color: #f0f0f0;
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* 스크롤 방지 */
    padding: 20px; /* 여백 조정 */
    position: relative;
    box-sizing: border-box;
}

/* 제목 */
.saju-intro1-title h1 {
    font-size: 3rem;
    color: #d4a017;
    margin: 10px 0;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5);
}

/* 중앙 레이아웃 */
.saju-intro1-layout {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px 0;
    flex-wrap: wrap; /* 화면이 좁아질 때 자동 줄바꿈 */
}

/* 아이콘 섹션 */
.saju-intro1-left-icons,
.saju-intro1-right-icons {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.saju-intro1-icon {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: #444;
    color: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.1rem;
    font-weight: bold;
    text-transform: uppercase;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
}

.saju-intro1-icon:hover {
    transform: translateY(-5px);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.5);
}

/* 중앙 이미지 */
.saju-intro1-image-container {
    margin: 0 30px;
}

.saju-intro1-image-container img {
    width: 220px;
    height: auto;
    border-radius: 15px;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease;
}

.saju-intro1-image-container img:hover {
    transform: scale(1.05);
}

/* 안내 텍스트 */
.saju-intro1-text {
    margin-top: 20px;
    font-size: 1.3rem;
    color: #dcdcdc;
    font-weight: 500;
    line-height: 1.6;
}

/* 우측 상단 다음 버튼 */
.saju-intro1-next-button {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: #4caf50;
    color: #fff;
    font-size: 1rem;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    font-weight: bold;
    text-transform: uppercase;
}

.saju-intro1-next-button:hover {
    background-color: #45a049;
    transform: translateY(-3px) scale(1.05);
}

/* 반응형 디자인 */
@media (max-width: 768px) {
    .saju-intro1-title h1 {
        font-size: 2.5rem;
    }

    .saju-intro1-icon {
        width: 80px;
        height: 80px;
        font-size: 1rem;
    }

    .saju-intro1-image-container img {
        width: 180px;
    }

    .saju-intro1-text {
        font-size: 1.1rem;
    }

    .saju-intro1-next-button {
        font-size: 0.9rem;
        padding: 8px 16px;
    }
}
